import '../styles/skill.scss'
import Bounce from 'react-reveal/Zoom';

const Skill = (props) => {

    return (
        <Bounce><div className = "skill">
            <img alt = {props.name} src = {"./images/skills/"+props.image+".png"} className = "skill__image"></img>
            <div className = "skill__name">{props.name}</div>
            {/*<div className = "skill__description">{props.description}</div> */}
        </div>
        </Bounce>

    )
}
export default Skill