import produce from 'immer';
import { initialState } from '../constants/constants';


   // Use the initialState as a default value
   export default function AppReducer(state = initialState, action) {
     // The reducer normally looks at the action type field to decide what happens  
     switch (action.type) {  
        //Normal Update
        case 'Something/doSomething' : {
           return {
                ...state,
                    property: action.payload
            }
        }

        //Update with immer
        case 'language/ChangeValue' : {
            return produce(state, draft => {
                draft.language = action.payload
            })   
        }

        case 'carousel/set/currentImage' : {
            return produce(state, draft => {
                draft.carouselCurrentImage = action.payload
            })  
        }

        case 'project/set/currentProject' : {
            return produce(state, draft => {
                draft.carouselCurrentProject = action.payload
                draft.carouselNbOfImages = 0
            })  
        }

        case 'carousel/set/currentNbOfImages' : {
            return produce(state, draft => {
                draft.carouselNbOfImages = action.payload;
            })  
        }

       default:      
       // If this reducer doesn't recognize the action type, or doesn't      
       // care about this specific action, return the existing state unchanged 
       return state 
   }}